<template>
  <v-card class="ml-1 mr-1 mb-1 mt-1">
    <v-card-title>
      <v-icon color="primary" left>code</v-icon>
      
      <v-spacer></v-spacer>
      <v-btn icon @click="$emit('up', idx)">
        <v-icon>keyboard_arrow_up</v-icon>
      </v-btn>
      <v-btn icon @click="$emit('down', idx)">
        <v-icon>keyboard_arrow_down</v-icon>
      </v-btn>
      <v-btn icon @click="$emit('deleteProcess', idx)">
        <v-icon color="error">mdi-delete</v-icon>
      </v-btn>
      <v-dialog>
        <template #activator="{on}">
          <v-btn v-on="on" icon>
            <v-icon>help</v-icon>
          </v-btn>
        </template>
        <v-card>
          <v-card-title class="headline">
            
          </v-card-title>
          <v-card-text>
            
            <br/>:
            <br/>
            <b>IEPL|IPLC</b>
          </v-card-text>
        </v-card>
      </v-dialog>
    </v-card-title>
    <v-card-text>
      
      <v-chip-group
          column
      >
        <v-chip
            v-for="(regex, idx) in regexps"
            :key="idx"
            close
            close-icon="mdi-delete"
            @click="edit(idx)"
            @click:close="remove(idx)"
        >
          {{ regex }}
        </v-chip>
      </v-chip-group>
      <v-text-field
          v-model="form.regex"
          append-icon="mdi-send"
          clear-icon="clear"
          clearable
          placeholder=""
          solo
          @click:append="add(form.regex)"
          @keyup.enter="add(form.regex)"
      />
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  props: ['args'],
  data: function () {
    return {
      mode: "IN",
      form: {
        regex: ""
      },
      regexps: [],
      idx: this.$vnode.key,
    }
  },
  methods: {
    add(keyword) {
      if (keyword) {
        this.regexps.push(keyword);
        this.form.regex = "";
      } else {
        this.$store.commit("SET_ERROR_MESSAGE", "");
      }
    },
    edit(idx) {
      this.form.regex = this.regexps[idx];
      this.remove(idx);
    },
    remove(idx) {
      this.regexps.splice(idx, 1);
    },
    save() {
      this.$emit("dataChanged", {
        idx: this.idx,
        args: this.regexps
      });
    }
  },
  watch: {
    regexps() {
      this.save();
    }
  },
  created() {
    this.regexps = this.args || [];
  }
}
</script>

